import React, { Component } from "react";
import Navbar from "./components/Navbar";
import VisualWindow from "./components/VisualWindow";
import userIcon from "./images/userIcon.png";
import { orderList } from "./algorithms/recommend";
import { Progress } from "./algorithms/recommend";
import { orderListNoDonate } from "./algorithms/recommendNoDonate";
import "./Buttons.css";
import "./SignWindow.css";

class AppWindow extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isSignIn: false,
      isSignUp: false,
      isMainWindow: true,
      isGovernor: false,
      isDonator: false,
      isLoggedIn: false,
      isRecommended: false,
      isWrongDetails: false,
      isCreated: false,
      isAcceptedPage: false,
      isPinPage: false,
      isWrongPIN: false,
      isPlanPage: false,
      isIncludeDonators: false,
      isPayDonation: false,
      acceptedList: [],
      acceptedIndex: 0,
      recommendedList: [],
      recommendIndex: 0,
      firstName: "",
      lastName: "",
      email: "",
      password: "",
      GovernorAccount: "",
      emailSignIn: "",
      passwordSignIn: "",
      db: new Map(),
      countSchools: 0,
      countHospitals: 0,
      countUniversities: 0,
      countMalls: 0,
      countResidentAreas: 0,
      countGasStations: 0,
      countCapital: 0,
      countPopulation: 0,
      countArea: 0,
      PINEntered: 0,
      DonateAmount: 0,
      CorrectPIN: 12345,
    };
    var num = 50;
    var n = num.toString();
  }

  //------------------- Navigation Bar -------------------
  renderNavComponents() {
    if (!this.state.isLoggedIn)
      return (
        <div className="content">
          <div className="navBar logo" onClick={this.handleHome}>
            <a>City Planner</a>
          </div>
          <div className="navBar right">
            <button className="Button Visualize" onClick={this.handleSignUp}>
              Sign Up
            </button>
            <button className="Button Visualize" onClick={this.handleSignIn}>
              Sign In
            </button>
          </div>
        </div>
      );
    else
      return (
        <div className="content">
          <div className="navBar logo" onClick={this.handleHome}>
            <a>City Planner</a>
          </div>
          <div className="navBar right">
            <img src={userIcon} height="20" className="image-center" />
            <a className="text-LogIn">
              {this.state.db.get(this.state.emailSignIn).dbFirstName}
            </a>
            {!this.state.isDonator && (
              <button
                className="Button Visualize"
                onClick={this.handleGovernor}
              >
                Recommender Form
              </button>
            )}
            <button className="Button Visualize" onClick={this.handleLogOut}>
              Log Out
            </button>
          </div>
        </div>
      );
  }
  //------------------------------------------------------

  //------------------- Main Window -------------------
  renderWindowComponents() {
    if (this.state.isMainWindow)
      return (
        <div className="content">
          <a className="message">
            Welcome to City Planner
            <br />
          </a>
          <a className="text">
            Get the most use of your capital to plan the cities based on the
            needs of the citizens. <br />
            <br />
          </a>
          <a className="text">Please Sign In to continue</a>
        </div>
      );
    else if (this.state.isSignUp)
      return (
        <div className="content">
          <div className="form-wrapper">
            <h1>Sign Up</h1>
            <form name="form1" onSubmit={this.handleSignUpSubmit} noValidate>
              <div className="firstName">
                <label htmlFor="firstName">First Name</label>
                <input
                  placeholder="First Name"
                  type="text"
                  name="firstName"
                  onChange={this.handleSignUpChange}
                  noValidate
                />
              </div>
              <div className="lastName">
                <label htmlFor="lastName">Last Name</label>
                <input
                  placeholder="Last Name"
                  type="text"
                  name="lastName"
                  onChange={this.handleSignUpChange}
                  noValidate
                />
              </div>
              <div className="email">
                <label htmlFor="email">Email</label>
                <input
                  placeholder="Email"
                  type="email"
                  name="email"
                  onChange={this.handleSignUpChange}
                  noValidate
                />
              </div>
              <div className="password">
                <label htmlFor="password">Password</label>
                <input
                  placeholder="Password"
                  type="password"
                  name="password"
                  onChange={this.handleSignUpChange}
                  noValidate
                />
              </div>
              <div className="radio-group">
                <label htmlFor="radio">Account Type</label>
                <label className="radio">
                  <span className="radio__input">
                    <input
                      type="radio"
                      name="radio"
                      value={true}
                      onChange={this.handleSignUpChange}
                    />
                    <span className="radio__control"></span>
                  </span>
                  <span className="radio__label">Governor</span>
                </label>
                <label className="radio">
                  <span className="radio__input">
                    <input
                      type="radio"
                      name="radio"
                      value={false}
                      onChange={this.handleSignUpChange}
                    />
                    <span className="radio__control"></span>
                  </span>
                  <span className="radio__label">Donator</span>
                </label>
              </div>
              <div className="createAccount">
                <button type="submit">Create Account</button>
              </div>
            </form>
            {this.state.isCreated && (
              <a className="text-accept">Account Created Successfully</a>
            )}
          </div>
        </div>
      );
    else if (this.state.isSignIn)
      return (
        <div className="content">
          <div className="form-wrapper">
            <h1>Sign In</h1>
            <form name="form2" onSubmit={this.handleSignInSubmit} noValidate>
              <div className="email">
                <label htmlFor="email">Email</label>
                <input
                  placeholder="Email"
                  type="email"
                  name="emailSignIn"
                  onChange={this.handleSignInChange}
                  noValidate
                />
              </div>
              <div className="password">
                <label htmlFor="password">Password</label>
                <input
                  placeholder="Password"
                  type="password"
                  name="passwordSignIn"
                  onChange={this.handleSignInChange}
                  noValidate
                />
              </div>
              <div className="createAccount">
                <button type="submit">Sign In</button>
              </div>
            </form>
            {this.state.isWrongDetails && (
              <a className="text-warning">Incorrect Log In Details</a>
            )}
          </div>
        </div>
      );
    // Display the Governor Page
    else if (this.state.isGovernor)
      return (
        <div className="content-left">
          <a className="form-heading">Please Fill In the following form</a>
          <div className="content-left">
            <div className="governor-input premises">
              <a className="input-description greenlighted">
                Number of Hospitals
              </a>
              <input
                placeholder="Hospitals"
                type="governor-input premises"
                name="Hospitals"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>
            <div className="governor-input premises">
              <a className="input-description greenlighted">
                Number of Schools
              </a>
              <input
                placeholder="Schools"
                type="governor-input premises"
                name="Schools"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>

            <div className="governor-input">
              <a className="input-description">
                Provide the estimated capital for this project
              </a>
              <input
                placeholder="Capital"
                type="governor-input"
                name="Capital"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>

            <div className="governor-input premises">
              <a className="input-description greenlighted">
                Number of Shopping Malls
              </a>
              <input
                placeholder="Shopping Malls"
                type="governor-input premises"
                name="ShoppingMalls"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>

            <div className="governor-input premises">
              <a className="input-description greenlighted">
                Number of Universities
              </a>
              <input
                placeholder="Universities"
                type="governor-input premises"
                name="Universities"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>

            <div className="governor-input">
              <a className="input-description">
                Provide the estimated population in the region of this project
              </a>
              <input
                placeholder="Population"
                type="governor-input"
                name="Population"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>

            <div className="governor-input premises">
              <a className="input-description greenlighted">
                Number of Gas Stations
              </a>
              <input
                placeholder="Gas Stations"
                type="governor-input premises greenlighted"
                name="GasStations"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>

            <div className="governor-input premises">
              <a className="input-description greenlighted">
                Number of Residential Areas
              </a>
              <input
                placeholder="Residential Areas"
                type="governor-input premises"
                name="ResidentialAreas"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>

            <div className="governor-input">
              <a className="input-description">
                Provide the estimated surface area required for this project
              </a>
              <input
                placeholder="Area"
                type="governor-input"
                name="Area"
                onChange={this.handleInputChange}
                noValidate
              />
            </div>
          </div>

          <div className="bottom">
            <button
              className="Button Visualize recommend-button"
              type="submit"
              onClick={this.handleRecommendation}
            >
              Get Recommendation
            </button>
          </div>
        </div>
      );
    else if (this.state.isRecommended) {
      if (
        this.state.recommendedList.length > 0 &&
        this.state.recommendIndex < this.state.recommendedList.length
      )
        return (
          <div className="content">
            <div className="recommend-box">
              <h1 className="text">
                {this.state.recommendedList[this.state.recommendIndex].name}
              </h1>
              <p className="recommend-text">
                Estimated Cost ={" "}
                {this.state.recommendedList[this.state.recommendIndex].cost} L.E
              </p>
              <p className="recommend-text">
                Estimated Area ={" "}
                {this.state.recommendedList[this.state.recommendIndex].area}{" "}
                meters
              </p>
              <p className="recommend-text">
              <br />
              {this.state.recommendedList[this.state.recommendIndex].descrip}
              </p>
            </div>
            <button
              className="Button Visualize Accept"
              onClick={this.handleAccept}
            >
              Accept
            </button>
            <button
              className="Button Visualize Decline"
              onClick={this.handleNext}
            >
              Retry
            </button>
          </div>
        );
      else
        return (
          <div className="content">
            <div className="recommend-box">
              <h1 className="text">
                Unfortunately you don't have any suggestions
              </h1>
              <p className="recommend-text">
                Your current input doesn't match any of the available
                recommendations. If you need an urgent recommendation, please
                alter your input data and then try again.
              </p>
            </div>
          </div>
        );
    } else if (this.state.isDonator) {
      if (
        this.state.acceptedList.length > 0 &&
        this.state.acceptedIndex < this.state.acceptedList.length
      )
        return (
          <div className="content">
            <div className="recommend-box1">
              <h1 className="text">
                {this.state.acceptedList[this.state.acceptedIndex].name}
              </h1>
              <br />
              <p className="recommend-text-small">Donation Progress</p>
              <p><Progress done={
                Math.round(((this.state.acceptedList[this.state.acceptedIndex].donateAmt/this.state.acceptedList[this.state.acceptedIndex].cost))*100)
                }/></p>
              <p className="recommend-text">
                Estimated Cost ={" "}
                {this.state.acceptedList[this.state.acceptedIndex].cost} L.E
              </p>
              <p className="recommend-text">
                Estimated Area ={" "}
                {this.state.acceptedList[this.state.acceptedIndex].area} meters
              </p>
              <p className="recommend-text">
              <br />
              {this.state.acceptedList[this.state.acceptedIndex].descrip}
              </p>
            </div>
            {this.state.acceptedIndex > 0 && (
              <button
                className="Button Visualize Toggle"
                onClick={this.handlePreviousDonator}
              >
                Previous
              </button>
            )}
            <button
              className="Button Visualize Accept"
              onClick={this.handleDonate}
            >
              Donate
            </button>
            {this.state.acceptedIndex < this.state.acceptedList.length - 1 && (
              <button
                className="Button Visualize Toggle"
                onClick={this.handleNextDonator}
              >
                Next
              </button>
            )}
          </div>
        );
      else
        return (
          <div className="content">
            <div className="recommend-box">
              <h1 className="text">
                Unfortunately there are no current projects require donations
              </h1>
              <p className="recommend-text">
                We will let you know once there is any of the projects that
                require donation. Thank you a lot for your consideration.
              </p>
            </div>
          </div>
        );
    } else if (this.state.isAcceptedPage)
      return (
        <div className="content">
          <div className="recommend-box">
            <h1 className="text">Thank you for the donation!</h1>
            <button
              className="Button Visualize Accept"
              onClick={this.handleReturnBack}
            >
              Return to Suggestions
            </button>
          </div>
        </div>
      );
    else if (this.state.isPinPage)
      return (
        <div className="content">
          <div className="recommend-box">
            <h1 className="text">Authorization PIN</h1>
            <input
              type="password"
              name="PIN"
              onChange={this.handleInputChange}
              noValidate
            />{" "}
            <br />
            <br />
            {this.state.isWrongPIN && (
              <a className="text-warning">PIN is wrong</a>
            )}
          </div>
          <button className="Button Visualize Accept" onClick={this.handlePIN}>
            Submit
          </button>
        </div>
      );
    else if (this.state.isPlanPage)
      return (
        <div className="content">
          <button
            className="Button Visualize recommend-button"
            type="submit"
            onClick={this.handlePlan1}
          >
            Plan 1
          </button>
          <br />
          <a className="text"> Plan your city with the help of donors</a>
          <br />
          <a className="text"></a>
          <br />
          <a className="text"></a>
          <br />
          <button
            className="Button Visualize recommend-button"
            type="submit"
            onClick={this.handlePlan2}
          >
            Plan 2
          </button>
          <br />
          <a className="text"> Plan your city with your capital</a>
        </div>
      );
    else if (this.state.isPayDonation)
      return (
        <div className="content">
          <div className="recommend-box">
            <h1 className="text">
              Please fill this information to complete your donation
            </h1>
            <input
              placeholder="Donation Amount"
              name="DonatAmt"
              onChange={this.handleInputChange}
              noValidate
            />
            <br />
            <br />
            <input
              placeholder="Credit Card Number"
              name="Dnt"
              onChange={this.handleInputChange}
              noValidate
            />
            <br />
            <br />
            <input
              placeholder="CVV Code"
              name="CCV"
              onChange={this.handleInputChange}
              noValidate
            />
            <br />
            <br />
          </div>
          <button
            className="Button Visualize Accept"
            onClick={this.handleSubmitDonation}
          >
            Donate
          </button>
        </div>
      );
  }
  //---------------------------------------------------

  render() {
    return (
      <div>
        <Navbar>{this.renderNavComponents()}</Navbar>
        <VisualWindow>{this.renderWindowComponents()}</VisualWindow>
      </div>
    );
  }

  //------------------- Sign Up -------------------
  handleSubmitDonation = () => {
    this.setState((prevState) => ({
      isPayDonation: false,
      isAcceptedPage: true,
    }));
  };

  handleReturnBack = () => {
    this.setState((prevState) => ({
      isDonator: true,
      isAcceptedPage: false,
    }));
  }

  handleDonate = () => {
    this.setState((prevState) => ({
      isPayDonation: true,
      isDonator: false,
    }));
  };

  handlePlan1 = () => {
    this.setState((prevState) => ({
      isIncludeDonators: true,
      isPlanPage: false,
      isGovernor: true,
    }));
  };

  handlePlan2 = () => {
    this.setState((prevState) => ({
      isIncludeDonators: false,
      isPlanPage: false,
      isGovernor: true,
    }));
  };

  handlePIN = () => {
    if (this.state.isIncludeDonators) {
      if (this.state.PINEntered == this.state.CorrectPIN) {
        this.setState((prevState) => ({
          indexAccepted: this.state.recommendIndex,
          acceptedList: this.state.acceptedList.concat(
            this.state.recommendedList[this.state.recommendIndex]
          ),
          isPinPage: false,
          isRecommended: true,
          recommendIndex: this.state.recommendIndex + 1,
          isWrongPIN: false,
        }));
      } else {
        this.setState((prevState) => ({
          isWrongPIN: true,
        }));
      }
    } else {
      if (this.state.PINEntered == this.state.CorrectPIN) {
        this.setState((prevState) => ({
          isPinPage: false,
          isRecommended: true,
          recommendIndex: this.state.recommendIndex + 1,
          isWrongPIN: false,
        }));
      } else {
        this.setState((prevState) => ({
          isWrongPIN: true,
        }));
      }
    }
  };

  handleSignUp = () => {
    this.setState((prevState) => ({
      isSignUp: true,
      isMainWindow: false,
      isSignIn: false,
      isWrongDetails: false,
      isCreated: false,
    }));
  };

  handleSignUpSubmit = (e) => {
    e.preventDefault();
    this.state.db.set(this.state.email, {
      dbPassword: this.state.password,
      dbFirstName: this.state.firstName,
      dbLastName: this.state.lastName,
      dbAccountType: this.state.GovernorAccount,
    });
    this.setState((prevState) => ({
      isCreated: true,
    }));
  };

  handleSignUpChange = (e) => {
    e.preventDefault();

    switch (e.target.name) {
      case "radio":
        this.state.GovernorAccount = e.target.value;
        break;
      case "firstName":
        this.state.firstName = e.target.value;
        break;
      case "lastName":
        this.state.lastName = e.target.value;
        break;
      case "email":
        this.state.email = e.target.value;
        break;
      case "password":
        this.state.password = e.target.value;
        break;
    }
  };
  //-----------------------------------------------

  //------------------- Sign In -------------------
  handleSignIn = () => {
    this.setState((prevState) => ({
      isSignIn: true,
      isMainWindow: false,
      isSignUp: false,
      isCreated: false,
    }));
  };

  handleSignInSubmit = (e) => {
    e.preventDefault();
    if (this.state.db.get(this.state.emailSignIn)) {
      if (
        this.state.passwordSignIn ==
        this.state.db.get(this.state.emailSignIn).dbPassword
      ) {
        if (this.state.db.get(this.state.emailSignIn).dbAccountType) {
          this.setState((prevState) => ({
            isLoggedIn: true,
            isWrongDetails: false,
          }));
          if (this.state.db.get(this.state.emailSignIn).dbAccountType == "true")
            this.handlePlan();
          else this.handleDonator();
        }
      }
    } else {
      this.setState((prevState) => ({
        isWrongDetails: true,
      }));
    }
  };

  handlePlan = () => {
    this.setState((prevState) => ({
      isSignIn: false,
      isPlanPage: true,
    }));
  };

  handleSignInChange = (e) => {
    e.preventDefault();

    switch (e.target.name) {
      case "emailSignIn":
        this.state.emailSignIn = e.target.value;
        break;
      case "passwordSignIn":
        this.state.passwordSignIn = e.target.value;
        break;
    }
  };
  //-----------------------------------------------

  //------------------- Home -------------------
  //--------------------------------------------
  handleHome = () => {
    this.setState((prevState) => ({
      isMainWindow: true,
      isSignUp: false,
      isSignIn: false,
      isWrongDetails: false,
      isCreated: false,
    }));
  };
  //--------------------------------------------

  //------------------- Governor Page -------------------
  //-----------------------------------------------------
  handleGovernor = () => {
    this.setState((prevState) => ({
      isSignIn: false,
      isSignUp: false,
      isMainWindow: false,
      isGovernor: true,
      isDonator: false,
      isWrongDetails: false,
      isCreated: false,
    }));
  };

  handleRecommendation = (e) => {
    e.preventDefault();
    if (this.state.isIncludeDonators) {
      this.setState((prevState) => ({
        recommendedList: this.state.recommendedList.concat(
          orderList(
            this.state.countSchools,
            this.state.countHospitals,
            this.state.countUniversities,
            this.state.countMalls,
            this.state.countResidentAreas,
            this.state.countGasStations,
            this.state.countCapital,
            this.state.countPopulation,
            this.state.countArea
          )
        ),
      }));
    } else {
      this.setState((prevState) => ({
        recommendedList: this.state.recommendedList.concat(
          orderListNoDonate(
            this.state.countSchools,
            this.state.countHospitals,
            this.state.countUniversities,
            this.state.countMalls,
            this.state.countResidentAreas,
            this.state.countGasStations,
            this.state.countCapital,
            this.state.countPopulation,
            this.state.countArea
          )
        ),
      }));
    }

    this.setState((prevState) => ({
      isSignIn: false,
      isSignUp: false,
      isMainWindow: false,
      isGovernor: false,
      isDonator: false,
      isRecommended: true,
      isWrongDetails: false,
      isCreated: false,
    }));
  };

  handleInputChange = (e) => {
    switch (e.target.name) {
      case "Hospitals":
        this.state.countHospitals = e.target.value;
        break;
      case "Schools":
        this.state.countSchools = e.target.value;
        break;
      case "Capital":
        this.state.countCapital = e.target.value;
        break;
      case "ShoppingMalls":
        this.state.countMalls = e.target.value;
        break;
      case "Universities":
        this.state.countUniversities = e.target.value;
        break;
      case "Population":
        this.state.countPopulation = e.target.value;
        break;
      case "GasStations":
        this.state.countGasStations = e.target.value;
        break;
      case "ResidentialAreas":
        this.state.countResidentAreas = e.target.value;
        break;
      case "Area":
        this.state.countArea = e.target.value;
        break;
      case "PIN":
        this.state.PINEntered = e.target.value;
        break;
      case "DonatAmt":
        this.state.acceptedList[this.state.acceptedIndex].donateAmt = e.target.value;
        break;
    }
  };

  handleAccept = () => {
    this.setState((prevState) => ({
      isRecommended: false,
      isPinPage: true,
    }));
  };

  handleNext = () => {
    this.setState((prevState) => ({
      recommendIndex: this.state.recommendIndex + 1,
    }));
  };

  handleNextDonator = () => {
    this.setState((prevState) => ({
      acceptedIndex: this.state.acceptedIndex + 1,
    }));
  };
  handlePreviousDonator = () => {
    this.setState((prevState) => ({
      acceptedIndex: this.state.acceptedIndex - 1,
    }));
  };
  //-----------------------------------------------------

  handleDonator = () => {
    this.setState((prevState) => ({
      isSignIn: false,
      isDonator: true,
    }));
  };

  //------------------- Log Out -------------------
  handleLogOut = () => {
    this.setState((prevState) => ({
      isSignIn: false,
      isRecommended: false,
      isSignUp: false,
      isMainWindow: true,
      isGovernor: false,
      isDonator: false,
      isLoggedIn: false,
      isWrongDetails: false,
      isCreated: false,
    }));
  };
  //-----------------------------------------------
}

export default AppWindow;
