import React, { Component } from "react";
import "./progressBar.css";
export function orderList(countSchools, countHospitals, countUniversities, 
                          countMalls, countResidentAreas, countGasStations, 
                          countCapital, countPopulation, countArea) 
{
    const hospitalsNeeded     = Math.ceil((2 * countPopulation) / 1000)
    const schoolsNeeded       = Math.ceil((0.5 * countPopulation) / 1000)
    const universitiesNeeded  = schoolsNeeded
    const gasStationsNeeded   = schoolsNeeded
    const MallsNeeded         = Math.ceil(countPopulation / 1000)
    const residentAreasNeeded = Math.ceil(512 * countPopulation / 1000)

    const premises = new Map()

    let suggestions = []

    premises.set("Hospital", {
        name: "Hospital",
        amountNeeded: hospitalsNeeded - countHospitals,
        cost: (112.5 * Math.pow(10, 6)),
        costNeeded: countCapital - (112.5 * Math.pow(10, 6)),
        area: 1500,
        areaNeeded: countArea - 1500,
        donateAmt: 0,
        descrip: "This institution will be equipped for diagnosing and treating diseases. It will provide both medical and surgical treatments for the sick and the injured people of different ages. In addition, it will be a centre for investigation and for teaching."
    })

    if(premises.get("Hospital").costNeeded   > 0 &&
       premises.get("Hospital").areaNeeded   > 0) suggestions.push(premises.get("Hospital"))

    premises.set("School", {
        name: "School",
        amountNeeded: schoolsNeeded - countSchools,
        cost: (30 * Math.pow(10, 6)),
        costNeeded: countCapital - (30 * Math.pow(10, 6)),
        area: 1344,
        areaNeeded: countArea - 1344,
        donateAmt: 0,
        descrip: "It is an educational institution designed to provide learning spaces and learning environments for students of different grades. The teaching process is under the supervision of teachers and officers. "
    })

    if(premises.get("School").costNeeded   > 0 &&
       premises.get("School").areaNeeded   > 0) suggestions.push(premises.get("School"))

    premises.set("University", {
        name: "University",
        amountNeeded: universitiesNeeded - countUniversities,
        cost: (40 * Math.pow(10, 6)),
        costNeeded: countCapital - (40 * Math.pow(10, 6)),
        area: 1730,
        areaNeeded: countArea - 1730,
        donateAmt: 0,
        descrip: "It is an institution for higher education where students take their degree from either in liberal arts or science fields. It also provides graduate research opportunities in various fields of study."
    })

    if(premises.get("University").costNeeded   > 0 &&
       premises.get("University").areaNeeded   > 0) suggestions.push(premises.get("University"))

    premises.set("Gas Station", {
        name: "Gas Station",
        amountNeeded: gasStationsNeeded - countGasStations,
        cost: (28 * Math.pow(10, 6)),
        costNeeded: countCapital - (28 * Math.pow(10, 6)),
        area: 700,
        areaNeeded: countArea - 700,
        donateAmt: 0,
        descrip: "It is a retail station for servicing motor vehicles especially with gasoline and oil. It provides the citizens with the fuel of transportation."
    })

    if(premises.get("Gas Station").costNeeded   > 0 &&
       premises.get("Gas Station").areaNeeded   > 0) suggestions.push(premises.get("Gas Station"))

    premises.set("Mall", {
        name: "Mall",
        amountNeeded: MallsNeeded - countMalls,
        cost: (12 * Math.pow(10, 6)),
        costNeeded: countCapital - (12 * Math.pow(10, 6)),
        area: 1000,
        areaNeeded: countArea - 1000,
        donateAmt: 0,
        descrip: "It is a collection of independent retail stores, services, and a parking area conceived, constructed, and maintained by a management firm as a unit. Citizens are served from it by paying their food, clothes, school supplies, electronics, electronic devices….etc."
    })

    if(premises.get("Mall").costNeeded   > 0 &&
       premises.get("Mall").areaNeeded   > 0) suggestions.push(premises.get("Mall"))

    premises.set("Residental Area", {
        name: "Residental Area",
        amountNeeded: residentAreasNeeded - countResidentAreas,
        cost: (4 * Math.pow(10, 6)),
        costNeeded: countCapital - (4 * Math.pow(10, 6)),
        area: 230,
        areaNeeded: countArea - 230,
        donateAmt: 0,
        descrip: "It is a land used in which housing predominates. It includes single-family housing, multi-family residential, or mobile homes to serve different types of families."
    })

    if(premises.get("Residental Area").costNeeded   > 0 &&
       premises.get("Residental Area").areaNeeded   > 0) suggestions.push(premises.get("Residental Area"))

    suggestions.sort(compare)

    return suggestions
}

function compare( a, b ) {
    if ( a.amountNeeded > b.amountNeeded ){
      return -1;
    }
    if ( a.amountNeeded < b.amountNeeded ){
      return 1;
    }
    return 0;
  }

export const Progress = ({done}) => {
	const [style, setStyle] = React.useState({});
	
	setTimeout(() => {
		const newStyle = {
			opacity: 1,
			width: `${done}%`
		}
		
		setStyle(newStyle);
	}, 200);
	
	return (
		<div className="progress">
			<div className="progress-done" style={style}>
				{done}%
			</div>
		</div>
	)
}